  
html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: 'Century Gothic';
    // font-size: 14px;
    // line-height: 22px;
  }
  
  img{
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  ul,
  ol {
    list-style: none;
  }
  
  a {
      text-decoration: none;
  }
  
  button {
    display: block;
    border: 0;
    background-color: transparent;
    cursor: pointer;
    color: inherit;
  }
  
  .visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
    margin: -1px;
  }